import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Api } from '../providers/api/api';
import { MoviesApi } from '../providers/api/movies-api';
import { ActorsApi } from '../providers/api/actors-api';
import { SeriesApi } from '../providers/api/series-api';
import { MoviesStorage } from '../providers/storage/movies-storage';
import { ActorsStorage } from '../providers/storage/actors-storage';
import { SeriesStorage } from '../providers/storage/series-storage';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/actor-details/actor-details.module.ngfactory#ActorDetailsModuleNgFactory', name: 'ActorDetails', segment: 'actors/:id', priority: 'low', defaultHistory: ['Actors'] },
    { loadChildren: '../pages/actors/actors.module.ngfactory#ActorsModuleNgFactory', name: 'Actors', segment: 'actors', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cinemas/cinemas.module.ngfactory#CinemasPageModuleNgFactory', name: 'CinemasPage', segment: 'cinemas', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discover/discover.module.ngfactory#DiscoverModuleNgFactory', name: 'Discover', segment: 'discover', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomeModuleNgFactory', name: 'Home', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/movie-details/movie-details.module.ngfactory#MovieDetailsModuleNgFactory', name: 'MovieDetails', segment: 'movies/:id', priority: 'low', defaultHistory: ['Movies'] },
    { loadChildren: '../pages/movies/movies.module.ngfactory#MoviesModuleNgFactory', name: 'Movies', segment: 'movies', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/serie-details/serie-details.module.ngfactory#SerieDetailsModuleNgFactory', name: 'SerieDetails', segment: 'series/:id', priority: 'low', defaultHistory: ['Series'] },
    { loadChildren: '../pages/search/search.module.ngfactory#SearchModuleNgFactory', name: 'Search', segment: 'search', priority: 'low', defaultHistory: ['Home'] },
    { loadChildren: '../pages/series/series.module.ngfactory#SeriesModuleNgFactory', name: 'Series', segment: 'series', priority: 'low', defaultHistory: [] }
  ]
}),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    MoviesApi,
    ActorsApi,
    SeriesApi,
    MoviesStorage,
    ActorsStorage,
    SeriesStorage,
    HttpClientModule,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    Api
  ]
})
export class AppModule {}



